
body {
    margin: 0;
}

main{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

main.dark{
    color: white;
    background-color: hsl(235, 21%, 11%);
}

main.dark .App{
    background-color: hsl(235, 24%, 19%);
    color: white;
}

main.dark .form-container{
    border-color: hsl(235, 21%, 11%);
}

main.dark .form-input{
    color: white;
}

main.dark .btn{
    color: white;
}

main.dark .clear-btn:hover{
    color: darkred;
}

main.dark input[type="checkbox"]{
    background-color: hsl(235, 21%, 11%);
    border: 0.3em solid white;
}

.App {
    background-color: rgb(248, 248, 248);
    padding: 25px;
    border-radius: 5px;
    box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1);
    width: 500px;
    font-family: 'monospace', sans-serif;
    font-weight: bold;
}

.main .App .dark{
    background-color: hsl(234, 11%, 52%);
}

.app-title{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 400px;
}

/* THEME */
.theme-icon{
    font-size: 2em;
    cursor: pointer;
}
.theme-icon:hover{
    opacity: 0.8;
}
/* FORM */

.form-container {
    border-bottom: 1px solid rgb(225, 225, 225);
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
}

input[type="checkbox"] {
    appearance: none;
    background-color: #fff;
    margin: 0;
    width: 1.5em;
    height: 1.5em;
    border: 0.3em solid black;
    transform: translateY(-0.075em);
    display: grid;
    place-content: center;
}

input[type="checkbox"]::before {
    content: "";
    width: 1em;
    height: 1em;
    transform: scale(0);
    transition: 120ms transform ease-in-out;
    background-color: orange;
}

input[type="checkbox"]:checked::before {
    transform: scale(1);
}

.form-input {
    border: none;
    padding: 15px;
    font-size: 1.2em;
    background-color: transparent;
    width: 100%;
}

.form-input:focus{
    outline: none;
}

/* TODO */
.todo-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 75px;
    gap: 15px;
}

.task {
    width: 100%;
}

.strikethrough {
    text-decoration: line-through;
    color: gray;
}

/* SETTINGS */

.todo-settings{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

/* BUTTONS */
.btn{
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.tab-btn{
    font-size: 0.8em;
}


.del-btn{
    font-size: 1.5em;
}

.tab-btn:hover{
    border-bottom: 2px solid orange;
}

.clear-btn{
    font-size: 0.8em;
}

.clear-btn:hover{
    color: darkred;
}

.add-btn{
    font-size: 1.5em;
}


/* MEDIA QUERY */

@media (max-width: 480px) {
    .App{
        width: 400px;
    }
}